import React from 'react';
import { Input, Row } from 'antd';
import Base from './Base';
import './Action.css';

export default class Action extends Base {
  constructor() {
    super();
    this.state = {

    };
  }

  onInit = ({actionBody}) => {
    actionBody = actionBody || {}
    this.setState({
       name: actionBody.name
    });
  };

  onConfirm = () => {
    const { name } = this.state;
    if (!name) {
      return {
        error: {
          type: 'error',
          message: '请输入名称'
        }
      };
    } else {
      return {
        actionBody: { name }
      };
    }
  };

  onNameChange = (e) => {
    this.setState({
      name: e.target.value
    });
  };

  render() {
    const { name } = this.state;

    return (
        <div className="plugin_action">
          <div>设置1：</div>
          <Row><Input value={name} onChange={this.onNameChange} /></Row>
          <div>设置2：</div>
          <Row><Input value={name} onChange={this.onNameChange} /></Row>
        </div>
    );
  }
}
